import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Loader from "Components/Loader";
import Helmet from "react-helmet";
import Message from "Components/Message";
import { Link } from "react-router-dom";

const Container = styled.div`
  height: calc(100vh - 50px);
  width: 100%;
  position: relative;
  padding: 50px;
`;

const Backdrop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
  filter: blur(3px);
  opacity: 0.5;
  z-index: 0;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  position: relative;
  z-index: 1;
  height: 100%;
`;
const Cover = styled.div`
  @media (max-width: 768px) {
    width: 100%;
    height: 300px;
  }
  @media (min-width: 768px) {
    width: 30%;
    height: 100%;
    border-radius: 5px;
  }
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
`;

const Data = styled.div`
  @media (max-width: 768px) {
    width: 70%;
  }
  width: 70%;
  margin-left: 10px;
`;

const Title = styled.h3`
  font-size: 32px;
  font-weight: 800;
  text-shadow: 4px 2px 2px gray;
  letter-spacing: 1px;
  margin-bottom: 20px;
`;

const AddInfo = styled.div`
  /* height: 83px; */
  overflow: hidden;
  width: 70%;
  margin-top: 20px;
  font-weight: 600;
  font-size: 10px;
`;

const ListItem = styled.li`
  margin-bottom: 10px;
  &:not(:last-child) {
    margin-right: 5px;
  }
  &:hover {
    color: blue;
  }
`;

const ItemContainer = styled.div`
  margin: 20px 0;
`;

const Item = styled.span`
  @media (max-width: 768px) {
    font-size: 9px;
  }
`;

const ReviewBtn = styled.div`
  margin-top: 15px;
  width: 150px;
  height: 40px;
  border: 1px solid gray;
  border-radius: 5px;
  display: grid;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 16px;
  letter-spacing: 2px;
  font-weight: 700;
  opacity: 0.5;
  margin-bottom: 15px;
  cursor: pointer;
  &:hover {
    background-color: white;
    color: black;
    opacity: 0.5;
  }
`;

const Divider = styled.span`
  @media (max-width: 768px) {
    margin: 0 2px;
  }
  margin: 0 10px;
`;

const Overview = styled.div`
  font-size: 12px;
  opacity: 0.7;
  width: 50%;
  line-height: 1.5;
`;

const ExternalLink = styled.a.attrs({
  target: "_blank"
})``;

const LogoTitle = styled.h1`
  margin-top: 20px;
  font-size: 13px;
  font-weight: 600;
  margin-bottom: 5px;
`;

const CompanyLogo = styled.img`
  height: 30px;
  width: 80px;
  display: block;
  margin-top: 10px;
  border: none;
`;

const Collection = styled.img`
  height: 150px;
  width: 150px;
  display: block;
  margin-top: 15px;
`;

const NoneImage = styled.div`
  display: none;
`;

const CountryName = styled.h1`
  margin-top: 10px;
  font-size: 16px;
`;

const Network = styled.img`
  width: 50px;
  height: 50px;
`;

const SeasonList = styled.ul`
  display: flex;
`;

const DetailPresenter = ({ result, error, loading, isMovie }) =>
  loading ? (
    <>
      <Helmet>
        <title>Loading | nomflix</title>
      </Helmet>
      <Loader />
    </>
  ) : error ? (
    <Message />
  ) : (
    <Container>
      <Helmet>
        <title>
          {result.original_title ? result.original_title : result.original_name}{" "}
          | Nomflix
        </title>
      </Helmet>
      <Backdrop
        bgImage={`https://image.tmdb.org/t/p/w300${result.backdrop_path}`}
      />
      <Content>
        <Cover
          bgImage={
            result.poster_path
              ? `https://image.tmdb.org/t/p/original${result.poster_path}`
              : require("../../assets/noPosterSmall.jpg")
          }
        />
        <Data>
          <Title>
            {result.original_title
              ? result.original_title
              : result.original_name}
          </Title>
          <ItemContainer>
            <Item>
              {result.genres &&
                result.genres.map((genre, index) =>
                  index === result.genres.length - 1
                    ? genre.name
                    : `${genre.name} / `
                )}
            </Item>
            <Divider />
            <Item>
              <i className="fas fa-calendar-alt" />
              {result.release_date
                ? `${result.release_date.substring(0, 4)}`
                : `${result.first_air_date.substring(0, 4)}`}
            </Item>
            <Divider />
            <Item>
              <i className="far fa-clock" />
              {result.runtime || result.episode_run_time
                ? `${result.runtime} min`
                : `${result.episode_run_time[0]} min`}
            </Item>
            <Divider />
          </ItemContainer>
          <ReviewBtn>
            <ExternalLink
              href={`https://cinemaone.net/title/${result.imdb_id}`}
            >
              Read Review
            </ExternalLink>
          </ReviewBtn>
          <Overview>{result.overview}</Overview>
          <AddInfo>
            <h3>
              <i className="fab fa-youtube" /> Youtube Link
            </h3>
            {result.videos &&
              result.videos.results.slice(0, 3).map((video, index) =>
                index === result.videos.results.length - 1 ? (
                  <ExternalLink
                    key={video.key}
                    href={`https://www.youtube.com/watch?v=${video.key}`}
                  >
                    {`🎥 ${video.name}`}
                  </ExternalLink>
                ) : (
                  <ul>
                    <ExternalLink
                      key={video.key}
                      href={`https://www.youtube.com/watch?v=${video.key}`}
                    >
                      <ListItem>{`🎥 ${video.name}`}</ListItem>
                    </ExternalLink>
                  </ul>
                )
              )}
            <LogoTitle>Production Company</LogoTitle>
            <CompanyLogo
              src={
                result.production_companies[0]
                  ? `https://image.tmdb.org/t/p/original${
                      result.production_companies[0].logo_path
                    }`
                  : "data:image/jpeg;base64,"
              }
            />
            {result.production_countries ? (
              <CountryName>
                제조국 : {result.production_countries[0].name}
              </CountryName>
            ) : null}
            {result.belongs_to_collection &&
            result.belongs_to_collection.poster_path ? (
              <Collection
                src={`https://image.tmdb.org/t/p/original${
                  result.belongs_to_collection.poster_path
                }`}
              />
            ) : (
              <NoneImage />
            )}
            {result.networks && result.networks[0].logo_path ? (
              <>
                <LogoTitle>Network</LogoTitle>
                <Network
                  src={`https://image.tmdb.org/t/p/original${
                    result.networks[0].logo_path
                  }`}
                />
              </>
            ) : (
              <NoneImage />
            )}
            <LogoTitle>Seasons</LogoTitle>
            <SeasonList>
              {result.seasons &&
                result.seasons.map((season, index) =>
                  season ? (
                    <>
                      <ListItem key={season.id}>
                        <Link to={`/${index}`}>{season.name}</Link>
                      </ListItem>
                    </>
                  ) : (
                    <NoneImage />
                  )
                )}
            </SeasonList>
          </AddInfo>
        </Data>
      </Content>
    </Container>
  );

DetailPresenter.propTypes = {
  result: PropTypes.object,
  error: PropTypes.string,
  loading: PropTypes.bool.isRequired,
  isMovie: PropTypes.bool.isRequired
};

export default DetailPresenter;
